.kDKRXa_i18nFontFamily {
  font-synthesis: weight;
  font-family: adobe-clean, Source Sans Pro, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Ubuntu, Trebuchet MS, Lucida Grande, sans-serif;
}

.kDKRXa_i18nFontFamily:lang(ar) {
  font-family: myriad-arabic, adobe-clean, Source Sans Pro, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Ubuntu, Trebuchet MS, Lucida Grande, sans-serif;
}

.kDKRXa_i18nFontFamily:lang(he) {
  font-family: myriad-hebrew, adobe-clean, Source Sans Pro, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Ubuntu, Trebuchet MS, Lucida Grande, sans-serif;
}

.kDKRXa_i18nFontFamily:lang(zh) {
  font-family: adobe-clean-han-traditional, source-han-traditional, MingLiu, Heiti TC Light, sans-serif;
}

.kDKRXa_i18nFontFamily:lang(zh-Hans) {
  font-family: adobe-clean-han-simplified-c, source-han-simplified-c, SimSun, Heiti SC Light, sans-serif;
}

.kDKRXa_i18nFontFamily:lang(zh-Hant) {
  font-family: adobe-clean-han-traditional, source-han-traditional, MingLiu, Microsoft JhengHei UI, Microsoft JhengHei, Heiti TC Light, sans-serif;
}

.kDKRXa_i18nFontFamily:lang(zh-SG), .kDKRXa_i18nFontFamily:lang(zh-CN) {
  font-family: adobe-clean-han-simplified-c, source-han-simplified-c, SimSun, Heiti SC Light, sans-serif;
}

.kDKRXa_i18nFontFamily:lang(ko) {
  font-family: adobe-clean-han-korean, source-han-korean, Malgun Gothic, Apple Gothic, sans-serif;
}

.kDKRXa_i18nFontFamily:lang(ja) {
  font-family: adobe-clean-han-japanese, Hiragino Kaku Gothic ProN, ヒラギノ角ゴ ProN W3, Osaka, YuGothic, Yu Gothic, メイリオ, Meiryo, ＭＳ Ｐゴシック, MS PGothic, sans-serif;
}

.kDKRXa_spectrum-FocusRing-ring {
  --spectrum-focus-ring-border-radius: var(--spectrum-textfield-border-radius, var(--spectrum-alias-border-radius-regular));
  --spectrum-focus-ring-gap: var(--spectrum-alias-input-focusring-gap);
  --spectrum-focus-ring-size: var(--spectrum-alias-input-focusring-size);
  --spectrum-focus-ring-border-size: 0px;
  --spectrum-focus-ring-color: var(--spectrum-high-contrast-focus-ring-color, var(--spectrum-alias-focus-ring-color, var(--spectrum-alias-focus-color)));
}

.kDKRXa_spectrum-FocusRing-ring:after {
  border-radius: calc(var(--spectrum-focus-ring-border-radius)  + var(--spectrum-focus-ring-gap));
  content: "";
  margin: calc(-1 * var(--spectrum-focus-ring-border-size));
  pointer-events: none;
  transition: box-shadow var(--spectrum-global-animation-duration-100, .13s) ease-out, margin var(--spectrum-global-animation-duration-100, .13s) ease-out;
  display: block;
  position: absolute;
  inset: 0;
}

.kDKRXa_spectrum-FocusRing.kDKRXa_focus-ring:after {
  margin: calc(var(--spectrum-focus-ring-gap) * -1 - var(--spectrum-focus-ring-border-size));
  box-shadow: 0 0 0 var(--spectrum-focus-ring-size) var(--spectrum-focus-ring-color);
}

.kDKRXa_spectrum-FocusRing--quiet:after {
  border-radius: 0;
}

.kDKRXa_spectrum-FocusRing--quiet.kDKRXa_focus-ring:after {
  margin: 0 0 calc(var(--spectrum-focus-ring-gap) * -1 - var(--spectrum-focus-ring-border-size)) 0;
  box-shadow: 0 var(--spectrum-focus-ring-size) 0 var(--spectrum-focus-ring-color);
}

@media (forced-colors: active) {
  .kDKRXa_spectrum-FocusRing, .kDKRXa_spectrum-FocusRing-ring, .kDKRXa_spectrum-FocusRing--quiet {
    --spectrum-high-contrast-focus-ring-color: Highlight;
  }

  :is(.kDKRXa_spectrum-FocusRing, .kDKRXa_spectrum-FocusRing-ring, .kDKRXa_spectrum-FocusRing--quiet):after {
    forced-color-adjust: none;
  }
}

.kDKRXa_spectrum {
  font-size: var(--spectrum-alias-font-size-default, var(--spectrum-global-dimension-font-size-100));
  color: var(--spectrum-body-text-color, var(--spectrum-alias-text-color));
}

.kDKRXa_spectrum, .kDKRXa_spectrum-Body, .kDKRXa_spectrum, .kDKRXa_spectrum-Body {
  font-size: var(--spectrum-body-4-text-size, var(--spectrum-alias-font-size-default));
  font-weight: var(--spectrum-body-4-text-font-weight, var(--spectrum-alias-body-text-font-weight));
  line-height: var(--spectrum-body-4-text-line-height, var(--spectrum-alias-body-text-line-height));
  font-style: var(--spectrum-body-4-text-font-style, var(--spectrum-global-font-style-regular));
}

.kDKRXa_spectrum-Body--italic {
  font-style: var(--spectrum-body-4-emphasis-text-font-style, var(--spectrum-global-font-style-italic));
}
